*{-webkit-box-sizing:border-box;box-sizing:border-box}body{font-family:'Open Sans',sans-serif;font-size:18px;color:#010000;font-weight:400;font-style:normal}a{text-decoration:none;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer}a:hover{color:#ff7c6d;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out}.container-fluid{line-height:24px}body{margin:0}header{position:fixed;display:block;width:100%;background-color:#5a5a5a;color:#fff;height:45px;z-index:10}.logo{font-size:20px;padding-top:.5rem;color:#fff;position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}#icon polyline{stroke:#fff}#icon:hover>polyline{stroke:#ff7c6d;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out}nav,nav ul,nav ul li{margin:0;padding:0}nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}nav ul{padding-right:1rem}nav ul li{list-style-type:none;display:inline-block;margin-right:10px}nav ul li.last{margin:0}nav ul li a{font-size:19px;line-height:normal;color:#fff}h1{display:block;width:100}.home-content{background:url(../photos/martin-sattler.jpg);background-size:cover;background-position:center;text-align:center;width:100%;min-height:100vh;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;-ms-flex-direction:column;flex-direction:column;position:relative;z-index:5}.home-content .banner{margin:auto;padding-top:2.75rem;border:4px double;height:18rem;width:28rem;color:#fff}.home-content .banner .name{font-weight:400;margin-bottom:1.5rem}.home-content .banner .title{font-size:1.25rem;font-weight:700;margin-bottom:1.5rem}.home-content .banner .location{margin-bottom:2rem}.home-content .banner .links{color:#fff;width:100%;height:32px;margin